/*  CORE MODULE STYLES */
html {
  font-size: 13px;
}
@media (max-width: 767px) {
  html {
    font-size: 12px;
  }
}

body {
  font-family: "Nunito Sans", sans-serif !important;
  color: #514d6a;
  background: #f2f4f8;
  line-height: 1.7;
  height: 100vh;
  overflow: hidden;
}
@media (max-width: 767px) {
  body {
    font-size: 12px;
  }
}
@media (min-width: 992px) {
  body {
    overflow-x: hidden;
  }
}

::-webkit-scrollbar {
  width: 2px;
}

/* Track */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  border-radius: 10px;
  background: #e8e8e8;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

::-webkit-scrollbar-thumb:window-inactive {
  background: #e8e8e8;
}

a {
  text-decoration: none;
  color: #74708d;
  transition: color 0.2s ease-in-out;
}
a:hover, a:active, a:focus {
  color: #0088ff;
  text-decoration: none;
}

input {
  outline: none !important;
  font-family: "Nunito Sans", sans-serif !important;
  color: #514d6a;
}

button,
input {
  box-shadow: none !important;
}

input[type=text],
input[type=password],
input[type=email],
textarea {
  appearance: none !important;
}

label {
  display: inline-block;
  margin-bottom: 0;
}

code {
  color: #74708d;
  background: #f2f4f8;
  display: inline-block;
  padding: 0.11rem 0.29rem;
  border-radius: 3px;
}

pre {
  color: #74708d;
  padding: 1.17rem;
  border-radius: 3px;
  border: 1px solid #e4e9f0;
  background: #f2f4f8;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

