.utils__example {
  padding: 0.88rem;
  border: 1px solid #e4e9f0;
}
.utils__example:before {
  content: "EXAMPLE";
  display: block;
  margin-bottom: 0.88rem;
  color: #74708d;
}
.utils__link--underlined {
  border-bottom: 1px solid rgba(57, 55, 73, 0.25);
}
.utils__link--underlined:hover {
  border-bottom-color: rgba(0, 136, 255, 0.2);
}
.utils__link--blue {
  color: #0088ff;
}
.utils__link--blue:hover {
  color: #0072d6;
}
.utils__link--blue.link-underlined {
  border-bottom: 1px solid rgba(0, 136, 255, 0.25);
}
.utils__link--blue.link-underlined:hover {
  border-bottom-color: rgba(0, 136, 255, 0.2);
}
.utils__iconsBlock {
  padding: 0 0 1.76rem;
  text-align: center;
}
.utils__iconsBlock p {
  height: 1.76rem;
}
.utils__iconsBlock li {
  text-align: center;
  display: inline-block;
  width: 2.35rem;
  height: 2.35rem;
  line-height: 2.35rem;
  font-size: 2.11rem;
  padding: 0.58rem 1.76rem;
  box-sizing: content-box;
  border-radius: 3px;
  background: #e8e8e8;
  margin: 0.58rem;
}
.utils__iconsBlock li:hover {
  background: #222034;
  color: #ffffff;
}
.utils__iconsBlock .tooltip {
  font-size: 1.05rem;
}
.utils__spin {
  -webkit-animation: fa-spin 1s infinite linear;
  -o-animation: fa-spin 1s infinite linear;
  animation: fa-spin 1s infinite linear;
}
.utils__spin-delayed {
  -webkit-animation: fa-spin-delayed 4s infinite ease-in-out;
  -o-animation: fa-spin-delayed 4s infinite ease-in-out;
  animation: fa-spin-delayed 4s infinite ease-in-out;
}
.utils__spin-delayed--pseudo-selector:before, .utils__spin-delayed--pseudo-selector:after {
  display: inline-block;
  -webkit-animation: fa-spin-delayed 3s infinite ease-in-out;
  -o-animation: fa-spin-delayed 3s infinite ease-in-out;
  animation: fa-spin-delayed 3s infinite ease-in-out;
}
.utils__codeCollapse .ant-collapse > .ant-collapse-item.ant-collapse-no-arrow > .ant-collapse-header {
  padding-left: 1.76rem;
  padding-right: 1.76rem;
}
.utils__codeCollapse .ant-collapse-borderless > .ant-collapse-item {
  margin-top: 1rem;
  border-top: 1px solid #e4e9f0;
  border-bottom: none;
}

/*  CORE ANIMATIONS */
@-webkit-keyframes fa-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}
@-webkit-keyframes fa-spin-delayed {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}
